<template>
<div>
<van-cell title="选择单个日期"  @click="show = true" />
<van-calendar v-model="show" @confirm="onConfirm" />
<van-field  v-model="mi" />
</div>
</template>
<script>
export default {
  data() {
    return {
    //   date: '',
      show: false,
      mi:'',
    };
  },
  methods: {
    formatDate(mi) {
      return `${mi.getFullYear()}/${mi.getMonth() + 1}/${mi.getDate()}`;
    },
    onConfirm(mi) {
      this.show = false;
      this.mi = this.formatDate(mi);
    }
  }
};
</script>


